
.profTemp1-cover{
	padding: 2vw 4vw!important;
}

.profTemp1-cover h3{
	font-family: sans-serif;
	font-weight: 600;
	padding: 1vw 0vw;
	margin: 0vw;
	font-size: 2.8vw;
	letter-spacing: .1vw;
}

/*name-------------------------------------------------*/
.profName-box{
	text-align: center;
	border-bottom: .1vw solid black;
	padding: 1.5vw 0vw;
}
.profName-box h1{
	font-size: 8vw;
	font-weight: bold;
	font-family: 'Poppins',sans-serif;
	letter-spacing: .4vw;
	padding-bottom: 0vw;
}
.profName-box h2{
	font-size: 2vw;
	font-family: 'poppins',sans-serif;
	letter-spacing: .3vw;
	
}

/*common ccs box----------------------------------------*/
.prof-box-common{
	padding: 1vw 0vw;
	border-bottom: .1vw solid black;

}
.prof-box-common ul{
	padding: 0vw ;
	margin-bottom: 1vw;
	font-family: 'Poppins',sans-serif;
	font-size: 1.6vw;
}
.prof-box-common ul li{
	margin: .3vw 0vw;
}
.prof-box-common p{
	font-size: 1.6vw;
	font-family: 'Poppins',sans-serif;
	margin-bottom: 1vw;
}
.prof-box-common span{
	font-weight: 600;
	font-family: 'Poppins',sans-serif;
	font-size: 1.8vw;
}
/*contact-----------------------------------------------*/
.profContact-box{
}
.profContact-box ul{
	list-style: none;	
}

.profContact-box ul .contact-list-icons{
	margin-right: .6vw;
}

/*skills-------------------------------------------------*/
.profSkill-box{

}
.profSkill-box h4{
	font-size: 1.9vw;
	font-family: sans-serif;
	font-weight: 600;
}
.profSkill-box ul{
	padding-left: 1.8vw;
	list-style: square;
}

/*education------------------------------------------------*/
.profEducation-box{

}
.profEducation-box .education-list-icons{
	margin-right: .6vw;
}

/*language--------------------------------------------------*/
.profLanguage-box{

}

/*summary---------------------------------------------------*/
.profSummary-box{
}
.profSummary-box p{
	text-align: justify;

}

/*project-----------------------------------------------------*/
.profProject-box{
}
.profProject-box .project-list-icons{
	margin-right: .6vw;
}

/*experience-------------------------------------------------*/
.profExperience-box{

}
.profExperience-box ul{
	padding-left: 1.8vw;
	list-style: square;
	font-size: 1.3vw;
	margin-bottom: 1.7vw;
}
.profExperience-box p{
	margin-bottom: .4vw;
}
.profExperience-box p span{
	display: inline-block;
	margin-bottom: .4vw;
}
.profExperience-box .education-list-icons{
	margin-right: .6vw;
}



/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
  .modal-dialog.modal-xl{
  	max-width: 98%;
  	margin:3vw auto;
  }
  .profTemp1-cover{
		padding: 1.9vw 4vw!important;
	}
  .profTemp1-cover h3{
  	font-size: 2.9vw;
  	padding: .8vw 0vw;
  }
  /*name-------------------------------------------------*/
	.profName-box{
		border-bottom: .01vw solid black;
	}
	.profName-box h1{
		font-size: 8vw;		
		margin-bottom: 0vw;
	}
	.profName-box h2{
		font-size: 2vw;		
	}

	/*common ccs box----------------------------------------*/
	.prof-box-common{
		padding: 1vw 1vw!important;
		border-bottom: .01vw solid black;
	}
	.prof-box-common ul{
		font-size: 1.7vw;
	}
	
	.prof-box-common p{
		font-size: 1.7vw;
	}
	.prof-box-common span{
		font-size: 1.9vw;
	}

	/*skills----------------------------------------------------	*/
	.profSkill-box h4{
		font-size: 2vw;
		margin-bottom: .3vw;
	}
	/*experience-------------------------------------------------	*/
	.profExperience-box p{
		margin-bottom: .5vw;
	}
	.profExperience-box p span{
		margin-bottom: .5vw;
	}
}


